<template>
  <div class="container">
    <form>
      <label>
        <input v-model="urlLink"
               placeholder="لینک دانلود خود را در این جا قرار دهید">
      </label>
      <button @click="CheckInputValue">
        ارسال
      </button>
    </form>
  </div>
</template>

<script>
import axios from 'axios'
import VueSimpleAlert from "vue-simple-alert";

export default {
  name: "Index",
  //========================================================================================
  data() {
    return {
      urlLink: null,
    }
  },
//==========================================================================================
  methods: {
    CheckInputValue(e) {
      e.preventDefault()
      if (this.urlLink === null || this.urlLink.trim() === '') {
        VueSimpleAlert.fire({
          title: "چطوری آخه؟",
          text: "لطفا لینک دانلود مدنظر خودتونو برام قرار بدین",
          type: "warning",
          timer: 2000,
          showConfirmButton: false,
        })
      } else {
        this.sendInputValue()
      }
    },
//==========================================================================================
    sendInputValue() {
      axios.post('linkService', {
        link: this.urlLink
      }).then(function () {
        VueSimpleAlert.fire({
          title: "خداروشکر",
          text: "خوشبختانه با موفقیت در لیست دانلود قرار گرفت",
          type: "success",
          timer: 2000,
          showConfirmButton: false,
        })
      }).catch(function () {
            VueSimpleAlert.fire({
              title: "وای خدای من",
              text: "متاسفانه یک جای کار مشکل داره؛ لینک دانلود پذیرفته نشد",
              type: "error",
              timer: 2000,
              showConfirmButton: false,
            })
          }
      )
    },
  },
}
</script>

